//global style
@font-face {
	font-family: GE_Hili_Book;
	src: url(../webfonts/GE_Hili_Book.otf);
}

.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "", sans-serif;

	.main-wrapper {
		background-color: #fff;
		overflow-x: hidden;
		background-image: url(../images/pattern.png);
		background-repeat: repeat;
		background-color: $second_color;
		// padding-top: 70px;
		position: relative;
		width: 100%;
	}
}

.sbsi7l-0.eARkMz {
	display: none;
}

//global style


//main section
section.main {
	padding: 0 0 70px;
	background-color: #fff;

	.main-title {
		text-align: center;
		background-color: #482D74;

		img {
			max-width: 350px;
		}
	}

	.content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 40px;

		.company-box {
			text-align: center;
			background-color: #fff;
			padding: 30px;
			transition: all ease 0.3s;
			border: 1px solid #eee;
			position: relative;

			// &::after {
			// 	content: "";
			// 	position: absolute;
			// 	left: 0;
			// 	top: 0;
			// 	width: 100%;
			// 	height: 100%;
			// 	background-color: $main_color;
			// 	z-index: -1;
			// }

			&:first-of-type {
				.image {
					img {
						width: 130px;
					}
				}
			}

			&:hover {
				transform: translate(0, -5px);
				box-shadow: 0 0.1rem 0 0 $main_color,
					0 0.1rem 0 0 $main_color;
			}

			.image {
				margin-bottom: 20px;

				img {
					width: 200px;
					object-fit: contain;
					-o-object-fit: contain;
					height: 130px;
				}

				i {
					font-size: 70px;
					color: $main_color;
				}
			}

			.title {
				h2 {
					font-size: 20px;
					color: $second_color;
					transition: all ease 0.3s;
					text-transform: capitalize;

					&:hover {
						color: $main_color;
					}
				}
			}
		}
	}
}

//main section

//single company
section.single-company {
	padding: $padding_section;
	background-color: #fff;

	.main-title {
		text-align: center;

		img {
			max-width: 200px;
			margin-bottom: 30px;
		}

		h1 {
			text-align: center;
			color: $second_color;
			font-weight: 600;
			font-size: 26px;
		}
	}

	.content {
		.text {
			margin-bottom: 30px;

			p {
				text-align: justify;
				line-height: 2.3;
				font-size: 17px;
			}
		}

		.company-btn {
			text-align: center;

			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//single company

//footer
footer {
	// padding: 50px 0;
	background-color: transparent;

	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.footer-logo {
			width: 200px;
		}
	}

	.footer-companies {
		ul {
			padding: 0;
			margin: 0;

			li {
				list-style: none;
				margin-bottom: 10px;
				display: flex;

				.icon {
					width: 25px;
					height: 25px;
					border-radius: 50%;
					background-color: #fff;
					margin-inline-end: 10px;
					@extend %center;
				}

				img {
					width: 20px;
				}

				&:last-of-type {
					margin-bottom: 0;
				}

				a {
					color: #fff;
					transition: all ease 0.3s;

					&:hover {
						color: $main_color;
					}
				}
			}
		}
	}
}

.copyrights {
	background-color: $second_color;
	padding: 15px 0;
	text-align: center;

	p {
		margin-bottom: 0;
		color: #fff;
		text-transform: capitalize;
	}
}

//footer